import React from 'react';
import Header from '../../Components/Header';
import { langTokens, useTheme } from '../../ThemeContext';
import styles from './Portfolio.module.css';

const Portfolio = () => {
  const { darkTheme, language } = useTheme();
  const langVar = langTokens(language)['portfolio'];

  return (
    <section
      className={`${darkTheme ? styles.darkTheme : ''} container`}
      id="portfolio"
    >
      <div className={styles.header}>
        <Header primary={langVar['header']} secondary={langVar['header2']} />
      </div>
      <div className={styles.content}>
        <div className={styles.pokeApi}>
          <a
            target="_blank"
            href="https://monumental-quokka-fec38b.netlify.app"
          >
            <img
              src="https://raw.githubusercontent.com/PokeAPI/media/master/logo/pokeapi_256.png"
              alt=""
            />
          </a>
        </div>
        <div></div>
        <div></div>
      </div>
    </section>
  );
};

export default Portfolio;
